import pListAddDialog from './list-add-dialog'

export default {
    data() {
        return {
            loading: true,
            list: []
        }
    },

    components: {
        pListAddDialog
    },

    mounted() {
        this.refresh()
    },

    methods: {
        refresh() {
            this.loading = true
            this.$store.dispatch('brandList', {
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        onAddClick() {
            this.$refs['addDialog'].showDialog({
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        remove(id) {
            this.$store.dispatch('brandDelete', {
                data: {id},
                success: () => {
                    this.refresh()
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        },

        onEdit(brand) {
            this.$refs['addDialog'].showDialog({
                brand,
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        onDelete(brand) {
            this.confirm(`确认要删除[${brand.name}]吗？`, () => {
                if (brand.model_info_list_count) {
                    this.confirm(`该品牌下还有${brand.model_info_list_count}个车型，真的要删除吗？`, () => {
                        this.remove(brand.id)
                    })
                } else {
                    this.remove(brand.id)
                }
            })
        }
    }
}