<style lang="less" src="./list.less"></style>

<template>
    <el-dialog :title="edit ? '编辑车型' : '添加车型'" :visible="open" width="500px" :before-close="onClose">
        <el-form ref="form" :model="form" label-width="90px" size="small">
            <el-form-item label="品牌" prop="brandId">
                <el-select v-model="form.brand_id" filterable placeholder="请选择">
                    <el-option
                    v-for="item in brandList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="车系名称" prop="series">
                <el-select v-model="form.series_id" filterable placeholder="请选择">
                    <el-option
                    v-for="item in seriesList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="车型名称" prop="model">
                <el-input v-model="form.model"></el-input>
            </el-form-item>
            <el-form-item label="生产年限" prop="produce_year">
                <el-select v-model="form.produce_year" filterable placeholder="请选择">
                    <el-option
                    v-for="item in yearList"
                    :key="item"
                    :label="item"
                    :value="item">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="外观" prop="appearance">
                <el-input
                    placeholder="输入颜色回车添加"
                    size="medium"
                    v-model="appearance"
                    @change="onAppearanceChange"
                    @keyup.enter="onAppearanceChange">
                </el-input>
            </el-form-item>
            <el-form-item label="外观" prop="appearance">
                <el-tag class="car-tag"  v-for="(it, i) in form.appearance" v-if="!it.is_del" :type="colorList[i%5]" :key="i" icon="">{{it.color}} <i class="el-icon-close" @click="delTag(i, 'appearance')"></i></el-tag>
            </el-form-item>
            <el-form-item label="内饰" prop="decoration">
                <el-input
                    placeholder="输入颜色回车添加"
                    size="medium"
                    v-model="decoration"
                    @change="onDecorationChange"
                    @keyup.enter="onDecorationChange">
                </el-input>
            </el-form-item>
            <el-form-item label="内饰" prop="decoration">
                <el-tag class="car-tag"  v-for="(it, i) in form.decoration" v-if="!it.is_del" :type="colorList[i%5]" :key="i" icon="">{{it.color}} <i class="el-icon-close" @click="delTag(i, 'decoration')"></i></el-tag>
            </el-form-item>
            <el-form-item label="指导价" prop="guide_price">
                <el-input v-model="form.guide_price"></el-input>
            </el-form-item>
            <el-form-item label="生产厂家" prop="manufactures">
                <el-input v-model="form.manufactures"></el-input>
            </el-form-item>
            <el-form-item label="规格" prop="rule">
                <el-select v-model="form.rule" filterable placeholder="请选择">
                    <el-option
                    v-for="item in ruleList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="open = false">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    // import {API_UPLOAD} from '../../../conf/urls'
    // import {BRAND_VALIDATOR} from '../../../conf/validator'
    import _ from 'lodash'

    export default {
        computed: {
            // uploadUrl: () => API_UPLOAD,
            // rules: () => BRAND_VALIDATOR
        },

        data() {
            return {
                open: false,
                edit: false,
                form: {
                    id: '',
                    brand_id: '',
                    brand_info: {},
                    series: '',
                    series_id: '',
                    model: '',
                    produce_year: 0,
                    guide_price: 0,
                    manufactures: '',
                    rule: '',
                    rule_desc: '',
                    appearance: [],
                    decoration: []
                },
                appearance: '',
                decoration: '',
                callback: null,
                brandList: [],
                yearList: [],
                seriesList: [],
                ruleList: [
                    {
                        id: 1,
                        name: '国产'
                    },
                    {
                        id: 2,
                        name: '中规'
                    },
                    {
                        id: 3,
                        name: '美规'
                    },
                    {
                        id: 4,
                        name: '加规'
                    },
                    {
                        id: 5,
                        name: '中东'
                    },
                    {
                        id: 6,
                        name: '欧版'
                    },
                    {
                        id: 7,
                        name: '墨版'
                    }
                ],
                colorList: ['primary', 'success', 'info', 'warning', 'danger']
            }
        },

        mounted() {
            require.ensure([], () => {
                this.$pinyin = require('../../../lib/pinyin')
            }, 'pinyin')
            this.yearList = this.getYearList()
        },

        methods: {
            getYearList() {
                let yearList = []
                let year = new Date().getFullYear()
                for (let i = 0; i < 40; i++) {
                    yearList.push(year - i)
                }
                return yearList
            },
            showDialog({onSuccess, car}) {
                this.open = true
                this.edit = !!car
                if (car) {
                    this.form = _.pick(car, ['id', 'brand_id', 'series_id', 'model', 'produce_year', 'guide_price', 'manufactures', 'rule', 'rule_desc', 'appearance', 'decoration'])
                }
                this.getBrands()
                this.getSeries()
                this.callback = onSuccess
            },

            onClose() {
                this.open = false
            },

            refreshPinyin() {
                this.form.pinyin = this.$pinyin.toPinYin(this.form.name)
            },

            handleAvatarSuccess(data) {
                this.form.logo = _.get(data, 'data.url', '')
            },

            getBrands() {
                this.$store.dispatch('brandList', {
                    success: data => {
                        this.brandList = data
                    },
                    error: msg => {
                        this.alert(msg)
                    }
                })
            },

            getSeries() {
                this.$store.dispatch('seriesList', {
                    success: data => {
                        this.seriesList = data
                    },
                    error: msg => {
                        this.alert(msg)
                    }
                })
            },

            onSubmit() {
                this.$refs['form'].validate(valid => {
                    if (valid) {
                        const id = this.form.series_id
                        this.form.series = this.seriesList.find(it => it.id === id).name
                        const appearance = JSON.stringify(this.form.appearance)
                        const decoration = JSON.stringify(this.form.decoration)
                        this.$store.dispatch(this.edit ? 'carEdit' : 'carAdd', {
                            data: {...this.form, appearance, decoration},
                            success: () => {
                                if (_.isFunction(this.callback)) {
                                    this.open = false
                                    this.callback()
                                }
                            },
                            error: msg => {
                                this.alert(msg)
                            }
                        })
                    }
                })
            },

            delTag(index, key) {
                const item = this.form[key][index];
                if(!item.id) {
                    this.form[key].splice(index, 1)
                } else {
                    item.is_del = true
                    this.form[key][index] = item
                    this.$set(this.form[key], index, item)
                }
            },

            onAppearanceChange(val) {
                const model_id = this.form.id
                const appearance = { color: val, model_id }
                this.form.appearance.push(appearance)
                this.appearance = ''
            },
            
            onDecorationChange(val) {
                const model_id = this.form.id
                const decoration = { color: val, model_id }
                this.form.decoration.push(decoration)
                this.decoration = ''
            }
        }
    }
</script>