<style lang="less" src="./list.less"></style>

<template>
    <el-dialog :title="edit ? '编辑品牌' : '添加品牌'" :visible="open" width="500px" :before-close="onClose">
        <el-form :rules="rules" ref="form" :model="form" label-width="90px" size="small">
            <el-form-item label="品牌名称" prop="name">
                <el-input v-model="form.name" @change="refreshPinyin"></el-input>
            </el-form-item>
            <el-form-item label="拼音" prop="pinyin">
                <el-input v-model="form.pinyin"></el-input>
            </el-form-item>
            <el-form-item label="logo" prop="logo">
                <el-upload
                        name="image"
                        :data="{key: 'brand'}"
                        class="avatar-uploader"
                        :action="uploadUrl"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess">
                    <img v-if="form.logo" :src="form.logo" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="open = false">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import {API_UPLOAD} from '../../../conf/urls'
    import {BRAND_VALIDATOR} from '../../../conf/validator'
    import _ from 'lodash'

    export default {
        computed: {
            uploadUrl: () => API_UPLOAD,
            rules: () => BRAND_VALIDATOR
        },

        data() {
            return {
                open: false,
                edit: false,
                form: {
                    id: '',
                    name: '',
                    pinyin: '',
                    logo: ''
                },
                callback: null
            }
        },

        mounted() {
            require.ensure([], () => {
                this.$pinyin = require('../../../lib/pinyin')
            }, 'pinyin')
        },

        methods: {
            showDialog({onSuccess, brand}) {
                this.open = true
                this.edit = !!brand
                if (brand) {
                    this.form = _.pick(brand, ['id', 'name', 'pinyin', 'logo'])
                } else {
                    this.form = {id: '', name: '', pinyin: '', logo: ''}
                }
                this.callback = onSuccess
            },

            onClose() {
                this.open = false
            },

            refreshPinyin() {
                this.form.pinyin = this.$pinyin.toPinYin(this.form.name)
            },

            handleAvatarSuccess(data) {
                this.form.logo = _.get(data, 'data.url', '')
            },

            onSubmit() {
                this.$refs['form'].validate(valid => {
                    if (valid) {
                        this.$store.dispatch(this.edit ? 'brandEdit' : 'brandAdd', {
                            data: this.form,
                            success: () => {
                                if (_.isFunction(this.callback)) {
                                    this.open = false
                                    this.callback()
                                }
                            },
                            error: msg => {
                                this.alert(msg)
                            }
                        })
                    }
                })
            }
        }
    }
</script>