import pListAddDialog from './list-add-dialog'
import _ from 'lodash'

export default {
    data() {
        return {
            loading: true,
            list: [],
            pagination: {
                total: 0,
                currentPage: 1,
                pageSize: 30
            },
            params: {
                brand_id: '',
                series_id: ''
                // page: 1
            },
            brandList: [],
            seriesList: [],
            seriesSelects: [],
            dstc_data: {
                produce_year: [],
                manufactures: [],
                rule_desc: [],
                // seriesList: []
            },
            colorList: ['primary', 'success', 'info', 'warning', 'danger']
        }
    },

    components: {
        pListAddDialog
    },

    async mounted() {
        await this.getBrands()
        await this.getSeries()
        // await Promise.all([pmsBrands, pmsSeries])
        this.refresh()
    },

    methods: {
        handleCurrentChange(val) {
            this.pagination.currentPage = val
        },
        refresh() {
            this.loading = true
            this.$store.dispatch('carList', {
                data: this.params,
                success: data => {
                    this.list = data.list
                    this.dstc_data = data.dstc_data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        onAddClick() {
            this.$refs['addDialog'].showDialog({
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        onSearchClick() {
            this.refresh()
        },

        remove(id) {
            this.$store.dispatch('carDelete', {
                data: {id},
                success: () => {
                    this.refresh()
                },
                error: msg => {
                    this.alert(msg)
                }
            })
        },
        
        getBrands() {
            return new Promise((resolve, reject) => {
                this.$store.dispatch('brandList', {
                    success: data => {
                        this.brandList = data
                        this.params.brand_id = _.isArray(data) && _.isObject(data[0]) ? data[0].id : ''
                        resolve()
                    },
                    error: msg => {
                        this.alert(msg)
                        reject()
                    }
                })

            })
        },

        initSeriesSelect() {
            const brand_id = this.params.brand_id
            this.seriesSelects = this.seriesList.filter(it => it.brand_id === brand_id)
            this.params.series_id = this.seriesSelects.length > 0 && _.isObject(this.seriesSelects[0]) ? this.seriesSelects[0].id : ''
        },

        getSeries() {
            return new Promise((resolve, reject) => {
                this.$store.dispatch('seriesList', {
                    success: data => {
                        this.seriesList = data
                        this.initSeriesSelect()
                        resolve()
                    },
                    error: msg => {
                        this.alert(msg)
                        reject()
                    }
                })
            })
        },
        onBrandChange() {
            this.initSeriesSelect()
        },
        onEdit(car) {
            this.$refs['addDialog'].showDialog({
                car,
                onSuccess: () => {
                    this.refresh()
                }
            })
        },

        onDelete(car) {
            this.confirm(`确认要删除[${car.model}]吗？`, () => {
                this.remove(car.id)
            })
        },

        filterProduceYear(val, row) {
            return row.produce_year == val
        },

        filterManufactures(val, row) {
            return row.manufactures == val
        },

        filterRuleDesc(val, row) {
            return row.rule_desc == val
        },
    }
}